.label{
    background-color: orange;
    padding: 20px;
    border-radius: 5px;
    box-shadow: 5px 5px 5px 2px  grey;
}
.percentage{
    text-align: center;
    background-color: gray;
    border-radius: 5px;
    box-shadow: 5px 5px 5px 2px  orange;
}
.statList{
    list-style: none;
    display: flex;
    background-color: aliceblue;
    justify-content: center;
    padding-left: 0;
    margin-top: 0;
}

.title{
    background-color: gray;
    font-size: 20px;
    padding: 20px 150px;
    text-align: center;
    margin-bottom: 0;
}
.item{
    display: flex;
    flex-direction: column;
    margin-right: 15px;
    font-size: 20px;
    padding: 20px;
}
.item:last-child{
    margin-right: 0;
}